export function parseTime(time, cFormat) {
  if (arguments.length === 0) {
    return null
  }
  if (!time) return ''
  const format = cFormat || 'yyyy-mm-dd hh:ii:ss'
  let date
  if (typeof time === 'object') {
    date = time
  } else {
    if (('' + time).length === 10) time = parseInt(time) * 1000
    date = new Date(time)
  }
  const formatObj = {
    y: date.getFullYear(),
    m: date.getMonth() + 1,
    d: date.getDate(),
    h: date.getHours(),
    i: date.getMinutes(),
    s: date.getSeconds(),
    a: date.getDay()
  }
  const time_str = format.replace(/(y|m|d|h|i|s|a)+/gi, (result, key) => {
    let value = formatObj[key]
    if (key === 'a') return ['一', '二', '三', '四', '五', '六', '日'][value - 1]
    if (result.length > 0 && value < 10) {
      value = '0' + value
    }
    return value || 0
  })
  return time_str
}

export function formatTime(time, option) {
  time = +time * 1000
  const d = new Date(time)
  const now = Date.now()

  const diff = (now - d) / 1000

  if (diff < 30) {
    return '刚刚'
  } else if (diff < 3600) { // less 1 hour
    return Math.ceil(diff / 60) + '分钟前'
  } else if (diff < 3600 * 24) {
    return Math.ceil(diff / 3600) + '小时前'
  } else if (diff < 3600 * 24 * 2) {
    return '1天前'
  }
  if (option) {
    return parseTime(time, option)
  } else {
    return d.getMonth() + 1 + '月' + d.getDate() + '日' + d.getHours() + '时' + d.getMinutes() + '分'
  }
}

export function isJSON(val) {
  if (typeof val !== 'string') {
    return false
  }
  try {
    const obj = JSON.parse(val)
    if (Object.prototype.toString.call(obj) === '[object Object]') {
      return true
    } else {
      return false
    }
  } catch (e) {
    return false
  }
}

// 随机数
export function random(start = 0, end = 100) {
  return Math.floor(Math.random() * (end - start)) + start
}
const humpReg = /\_(\w)/g
export function toHump(name) {
  return !name ? '' : name.replace(humpReg, function(x) { return x.slice(1).toUpperCase() })
}

export function ArrToObj(arr) {
  if (!Array.isArray(arr)) {
    return {}
  }
  return arr.reduce((obj, d) => {
    obj[d.field] = d.value || 0
    return obj
  }, {})
}

export function toPolygonPoint(region) {
  if (!region) {
    return []
  }
  return region.split(',').map(point => {
    const latlng = point.split(' ')
    return [Number(latlng[1]), Number(latlng[0])]
  })
}

export function isNumber(val) {
  return /^[0-9]+.?[0-9]*$/.test(val)
}
