import { requestAnimationFrame, cancelAnimationFrame } from './requestAnimationFrame.js'
const EventEmitter = require('events')
const toString = Object.prototype.toString
// function isPromise(target) {
//   return toString.call(target) === "[object Promise]"
// }
function isFunction(target) {
  return toString.call(target) === "[object Function]"
}
function S4() {
  return (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1)
}
function guid() {
  return (S4() + S4() + "-" + S4() + "-" + S4() + "-" + S4() + "-" + S4() + S4() + S4())
}
export class Job extends EventEmitter {
  constructor(interval, params, now) {
    super()
    this.id = guid()
    this.interval = Math.max(1000, interval) // 最小时间隔 1000ms
    this.time = now === true ? Date.now() : Date.now() + this.interval
    this.params = params
    this.delFlag = false
    this.isRunning = false
  }
  nextTime() {
    this.isRunning = false
    this.time = Date.now() + this.interval
  }
  delete() {
    this.delFlag = true
  }
  run() {
    // console.log("Job", this.id, this.time, toString.call(this.params))

    if (isFunction(this.params)) {
      if (this.params() === false) {
        this.delFlag = true
      } else {
        this.nextTime()
      }
    } else {
      // TODO...
      // 执行完成后如果需要再继续
      this.nextTime()
    }
  }
}
export class AjaxJob extends Job {
  constructor(interval, params, now) {
    super(interval, params, now)
    this.ajax = null
  }

  run() {
    // do ajax
    console.log("Job", this.id, this.time, this.params)
  }
}
export class Scheduler extends EventEmitter {
  constructor(opts) {
    super()
    this.opts = opts
    this.jobList = new Map()
  }
  add(job) {
    this.jobList.set(job.id, job)
  }
  remove(job) {
    this.jobList.delete(job.id)
  }
  dojob() {
    this.jobList.forEach((job) => {
      if (job.delFlag === true) {
        job.emit('delete')
        this.remove(job)
      } else if (job.isRunning !== true && job.time <= Date.now()) {
        setTimeout(function() {
          job.isRunning = true
          job.run()
        }, 0)
      }
    })
    this.ra = requestAnimationFrame(this.dojob.bind(this))
  }
  run() {
    this.ra = requestAnimationFrame(this.dojob.bind(this))
  }
  stop() {
    cancelAnimationFrame(this.ra)
  }
}

