export const format = num => {
  if (num || num === 0 || num === '0') {
    const thisNum = parseFloat(num)
    if (thisNum.toString().indexOf('.') !== -1) {
      var b = thisNum.toLocaleString()
      return b
    } else {
      var c = thisNum.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')
      return c
    }
  }
}

export const toFix = num => {
  if (num || num === 0 || num === '0') {
    const thisNum = parseFloat(num)
    return thisNum.toFixed(1)
  }
}

export const toFix2 = num => {
  if (num || num === 0 || num === '0') {
    const thisNum = parseFloat(num)
    return thisNum.toFixed(2)
  }
}

export const substrStr = str => {
  if (str) {
    if (str.length > 8) {
      const newstr = str.substr(0, 8)
      return newstr + '...'
    } else {
      return str
    }
  }
}

export const checkStatus = code => {
  const data = [
    {
      code: 0,
      name: '待审批'
    },
    {
      code: 50,
      name: '同意'
    },
    {
      code: 51,
      name: '进行中'
    },
    {
      code: 70,
      name: '已完成'
    },
    {
      code: 75,
      name: '异常'
    },
    {
      code: 80,
      name: '取消'
    },
    {
      code: 100,
      name: '拒绝'
    }
  ]
  if (code) {
    let name = ''
    data.map(item => {
      if (item.code === code) {
        name = item.name
      }
      return name
    })
    return name
  }
}
