<template>
    <div id="app">
        <!-- <transition :name='transitionName'>
          <keep-alive include="placeOrder"> -->
        <div id="jumpLayer">
            <div class="jumpLayer_wrap">
                <div class="loader-15"></div>
            </div>
        </div>
        <router-view></router-view>
        <!-- </keep-alive>
      </transition> -->
        <!-- <div class="version"> <span class="fr mr5">版本: v{{ VUE_APP_VERSION }}</span></div> -->
    </div>
</template>

<script>
    import yuying from '@/mixins/yuying.js'
    import {Scheduler, Job} from '@/utils/scheduler.js'
    import {login} from '@/utils/request.js'

    export default {
        name: 'app',
        mixins: [yuying],
        provide() {
            return {
                app: this,
                transitionName: 'fold-left'
            }
        },
        data() {
            return {
                VUE_APP_VERSION: process.env.VUE_APP_VERSION
            }
        },
        computed: {
            menuStyle() {
                return {
                    zoom: this.scaling.zoom
                }
            }
        },
        watch: {
            // 如果路由有变化，会再次执行该方法
            '$route'(to, from) {
                // 如果to索引大于from索引,判断为前进状态,反之则为后退状态
                if (to.meta.index > from.meta.index) {
                    this.transitionName = 'fold-left'
                } else {
                    this.transitionName = 'fold-right'
                }
            }
        },
        methods: {
            // 自动播放 job
            initAutoJob() {
                this.autoJob = new Job(10 * 1000, () => {
                    document.querySelectorAll('.map_button')[0].children[1].click()
                    this.deleteAutoJob()
                })
                this.Scheduler.add(this.autoJob)
            },
            jump(target) {
                this.$router.push(target)
            },
            deleteAutoJob() {
                this.autoJob && this.autoJob.delete()
            }
        },
        beforeCreate() {
            // var html = document.getElementsByTagName("html")[0]
            // var oWidth = document.body.clientWidth || document.documentElement.clientWidth
            // html.style.fontSize = oWidth /1440 * 16 + "px"
            this.Scheduler = new Scheduler()
            this.Scheduler.run()

            this.loginJob = new Job(60 * 60 * 1000, login)
            this.Scheduler.add(this.loginJob)
        },
        mounted() {
        }
    }
</script>
